@import "../src/colony_table.css";

* {
  max-width: 100%;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 10px auto 10px auto;
  text-align: center;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", sans-serif;
}

span {
  font-style: italic;
}

canvas {
  image-rendering: optimizeSpeed; /* Older versions of FF */
  image-rendering: -moz-crisp-edges; /* FF 6.0+ */
  image-rendering: -webkit-optimize-contrast; /* Safari */
  image-rendering: -o-crisp-edges; /* OS X & Windows Opera (12.02+) */
  image-rendering: pixelated; /* Awesome future-browsers */
  -ms-interpolation-mode: nearest-neighbor; /* IE */
}

section,
footer {
  margin: 10px;
}

section#simulation {
  display: flex;
}

canvas#canvas {
  flex-shrink: 0;
}

.flex-right {
  margin: 10px;
}

section#configure {
  display: flex;
  justify-content: flex-start;
}

a {
  text-align: center;
  margin: 0 auto;
}

section#configure > div {
  margin: 0 10px;
}

.center-everything {
  text-align: center;
  width: 500px;
}

label {
  display: inline-flex;
  width: 100%;
}
label input,
label select {
  flex: 1;
  margin: 2px 2px 2px 5px;
  padding: 2px;
}
input,
select,
label {
  cursor: pointer;
}

footer {
  font-size: 10px;
}

@media (orientation: landscape) {
  section {
    flex-direction: row;
  }
  section#simulation {
    height: 95%;
  }
  canvas#canvas {
    height: 100%;
  }
  hgroup {
    font-size: 3rem;
    margin: 30px auto 30px auto;
  }
}

@media (orientation: portrait) {
  section {
    flex-direction: column;
  }
  canvas#canvas {
    width: 100%;
  }
  hgroup {
    font-size: 2rem;
    margin: 30px auto 30px auto;
  }
}
div.input-group-prepend {
  padding: 0px;
}
.input-group-text {
  width: 200px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding: 0.375rem 0.75rem;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  text-align: center;
  white-space: nowrap;
  background-color: #e9ecef;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: 0px;
}

.input-group {
  display: flex;
  padding: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  position: relative;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  margin-bottom: 0;
}
#start_game {
  margin: 0 auto;
  display: block;
  width: 200px;
}

.form-control {
  display: block;
  width: 400px;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  border-bottom-left-radius: 0px;
  border-top-left-radius: 0px;
}

.btn-success {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
}

.btn {
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.custom-control {
  display: flex;
}

@media screen and (max-width: 561px) {
  .input-group {
    flex-direction: column;
  }

  .input-group-text,
  .input-group input:not([type="checkbox"]),
  .input-group select {
    width: 100%;
  }

  .input-group-text {
    border-right: 1px solid #ced4da;
    border-bottom: none;
    border-radius: 0.25rem 0.25rem 0 0;
  }

  .input-group input,
  .input-group select {
    border-top: none;
    border-radius: 0 0 0.25rem 0.25rem;
  }
}
